import { Component, OnInit } from '@angular/core';
import { AppInfo } from '../../models';
import { AppService } from '../../services/app.service';
import { ActivatedRoute } from '@angular/router';
import { switchMap } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { PageInfo } from 'src/app/shared/models';

@Component({
  selector: 'app-default-page',
  template: `
  <app-page-header [icon]="page.icon" [title]="page.title"></app-page-header>
  <div class="d-flex flex-column text-center" *ngIf="appInfo$ | async as appInfo">
    <div *ngIf="appInfo.logo"><img [src]="appInfo.logo"></div>
    <h1>{{appInfo.title}}</h1>
    <h4>{{appInfo.description}}</h4>
  </div>
  `,
  styles: []
})
export class DefaultPageComponent implements OnInit {

  appInfo$: Observable<AppInfo>;

  page: PageInfo = {
    title: 'Home',
    icon: 'icon-home icons font-2xl'
  } as PageInfo;

  constructor(private appService: AppService) { }

  ngOnInit() {
    this.appInfo$ = this.appService.currentApp$;
  }
}
