import { Component, OnInit, Input } from '@angular/core';
import { LinkInfo, AppInfo } from '../../models';
import { ActionsService } from '../../services/actions.service';

@Component({
  selector: 'app-aside-content',
  templateUrl: 'aside-content.component.html',
  styles: []
})
export class AsideContentComponent implements OnInit {


  @Input() list: LinkInfo[];
  @Input() appInfo: AppInfo;

  constructor(private actionsService: ActionsService) { }

  ngOnInit() {
  }

  onSelect(evt) {
    this.actionsService.handleAction(this.appInfo, evt);
  }

}
