import { Component, OnInit, Input, ViewChild, ElementRef, AfterViewInit, Output, EventEmitter } from '@angular/core';
import { Ace, edit } from 'ace-builds';
import { formatObjToJsonStr } from 'src/app/shared';
import { AppInfo} from '../../models';
import { ModalDirective } from 'ngx-bootstrap';

@Component({
  selector: 'app-app-config',
  templateUrl: 'app-config.component.html',
  styles: [`
    :host {
      display: flex;
      flex-direction: column;
    }
  `]
})
export class AppConfigComponent implements AfterViewInit {

  @Input() config: AppInfo;
  error: string;
  @ViewChild('appConfigElement', {static: false}) appConfigElement: ElementRef;
  appEditor: Ace.Editor;
  @Output() save = new EventEmitter<AppInfo>();
  @Input() modalRef: ModalDirective;

  constructor() { }

  ngAfterViewInit() {
    this.appEditor = edit(this.appConfigElement.nativeElement, {
      mode: 'ace/mode/json',
      autoScrollEditorIntoView: true,
      value: formatObjToJsonStr(this.config)
    });
  }

  saveApp() {
    try {
      const appInfo = JSON.parse(this.appEditor.getValue());
      this.save.emit(appInfo);
      this.modalRef.hide();
    } catch (e) {
      this.error = e.message;
    }
  }
}
