import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// Import Containers
import { AppsComponent } from './components/available-apps/apps.component';
import { CustomAppLayoutComponent } from './components/custom-app-layout/custom-app-layout.component';
import { DynamicPageComponent } from './components/dynamic-page/dynamic-page.component';
import { DefaultPageComponent } from './components/default-page/default-page.component';

import { PageTitleResolver } from './components/dynamic-page/dynamic-page.guard';
import { AppConfigGuard } from './components/available-apps/app-config.guard';

const routes: Routes = [
  {
    path: '',
    component: AppsComponent
  },
  {
    path: ':appName',
    component: CustomAppLayoutComponent,
    data: {
      title: 'Home'
    },
    canActivate: [AppConfigGuard],
    children: [
      {
        path: '',
        component: DefaultPageComponent,
      },
      {
        path: ':pageId',
        component: DynamicPageComponent,
        resolve: {
          title: PageTitleResolver
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CustomAppRoutingModule { }
