import { LinkInfo, NavItem } from './models';

export function groupLinkInfos(items: LinkInfo[]): NavItem[] {
  const groups = {};
  const sItems: NavItem[] = [];

  items.forEach(item => {
    if (item.groupName) {
      const isFirst = !groups[item.groupName];
      groups[item.groupName] = groups[item.groupName] || createSidebarItem(item, true);

      if (isFirst) {
        sItems.push(groups[item.groupName]);
      }
      groups[item.groupName].children.push(createSidebarItem(item));
    } else {
      sItems.push(createSidebarItem(item));
    }
  });

  return sItems;
}

function createSidebarItem(item: LinkInfo, group = false) {
  const name = group ? item.groupName : item.name;
  const sItem: any = {
    url: 'http',
    name, // TODO: Set name: item.title || name
    icon: group ? 'icon-cursor' : item.icon,
    attributes: {
      name,
      actionName: group ? null : item.actionName
    }
  };

  if (group) {
    sItem.children = [];
  }

  return sItem;
}
