import { Injectable } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';

import { registerAction, ExecutionContext, WorkflowEngine, resolveValue, JSONUtils, ActionStatus } from '@ngx-dynamic-components/core';
import { ModalService } from './modal.service';
import { LinkInfo, AppInfo, HttpGetConfig, ShowPageConfig, GetJsonValueConfig,
   AppContextConfig, SetJsonValueConfig, SqlDataConfig, UniqueValuesConfig } from '../models';
import { AppService } from './app.service';
import { SqlDbService } from 'src/app/shared/services';
import { getErrorMessage } from 'src/app/shared';

@Injectable({
  providedIn: 'root'
})
export class ActionsService {

  private _appContext = {};

  get appContext() {
    return this._appContext;
  }

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private modalService: ModalService,
    private httpClient: HttpClient,
    private sqlDbService: SqlDbService,
    private appService: AppService) {
      this.addAppServices();
      this.addCommonActions();
      this.addHttpActions();
    }

  /**
   * Handles action.
   * @param payload Action payload.
   */
  handleAction(appInfo: AppInfo, payload: LinkInfo) {
    if (payload.actionName === 'showPage') {
      this.showPage(appInfo, payload.name);
    } else {
      console.warn(`Unregistered action ${payload.actionName}`, payload);
    }
  }

  private async showPage(appInfo: AppInfo, pageName: string) {
    try {
      const page = await this.appService.getPageInfo(appInfo.ownerName, appInfo.appName, pageName).toPromise();
      if (page.showInDialog) {
        this.modalService.open(page, this.appContext);
      } else {
        return this.router.navigate(['app', appInfo.ownerName, appInfo.appName, page.pageName]);
      }
    } catch (e) {
      console.error(e);
    }
  }

  private addAppServices() {
    registerAction('showPage', (context: ExecutionContext, config: ShowPageConfig) => {
      this.showPage(this.appService.currentApp, config.pageName);
    });

    registerAction('getJsonValue', async (context: ExecutionContext, config: GetJsonValueConfig) => {
      return await this.appService.getJsonValue(config.jsonKey).toPromise();
    });

    registerAction('setJsonValue', async (context: ExecutionContext, config: SetJsonValueConfig) => {
      const objValue = resolveValue(context, config.object);
      const value = JSONUtils.find(objValue, config.propertyName);
      await this.appService.setJsonValue(config.jsonKey, value).toPromise();
      return value;
    });
  }

  private addCommonActions() {
    registerAction('appContext', async (context: ExecutionContext, config: AppContextConfig) => {
      const targetWorkflow = this.appContext[config.componentId] as WorkflowEngine;
      targetWorkflow.setVariable(config.propertyValue, context.variables.get(config.propertyValue));
      return await targetWorkflow.run(config.appContextAction);
    });

    registerAction('closeDialog', () => {
      return this.modalService.close();
    });
  }

  private addHttpActions() {
    registerAction('httpGet', async (context: ExecutionContext, config: HttpGetConfig) => {
      return await this.httpClient.get(config.url).toPromise();
    });

    registerAction('getSqlData', async (context: ExecutionContext, config: SqlDataConfig) => {
      try {
        return await this.sqlDbService.selectSqlObjectData('sql-shared', config.objectOwner, config.objectName, {
          filters: this.getFilters(context, config)
        }).toPromise();
      } catch (e) {
        return {
          status: ActionStatus.FAILED,
          message: getErrorMessage(e)
        };
      }
    });

    registerAction('getUniqueValues', (context: ExecutionContext, config: UniqueValuesConfig) => {
      const fromObj = resolveValue(context, config.object);
      const table = JSONUtils.find(fromObj, config.propertyName || '$');
      const values: [] = table.map(row => row[config.key]);
      return Array.from(new Set(values));
    });
  }

  private getFilters(context: ExecutionContext, config: SqlDataConfig) {
    if (!config.filters) {
      return [];
    }

    return config.filters.map(filter => {
      let value: any;
      if (filter.value === '$QUERY') {
        value = this.route.snapshot.queryParams[filter.fieldName];
      } else {
        value = resolveValue(context, filter.value);

        // TODO: Remove.
        if (value.length) {
          value = value.join(',');
        }
      }
      return {
        ...filter,
        value
      };
    });
  }
}
