import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';
import { map, filter } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { AppService } from '../../services/app.service';

@Injectable({
  providedIn: 'root'
})
export class PageTitleResolver implements Resolve<Observable<any>> {

  constructor(private appService: AppService) {}

  resolve({params, parent}: ActivatedRouteSnapshot): Observable<string> {
    return this.appService.getPageInfo(parent.params.owner, parent.params.appName, params.pageId).pipe(
      filter(p => Boolean(p)),
      map(p => p.title));
  }
}
