import { Injectable } from '@angular/core';
import { AppInfo, AppLinkInfo } from '../models';
import { Observable, of, BehaviorSubject } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { map, share } from 'rxjs/operators';
import { AuthService } from 'src/app/security';

// Dev data.
import * as testApp from '../_temp-configs/config';
import * as todoApp from '../_temp-configs/todo.config';
import * as wijmoApp from '../_temp-configs/wijmo.config';
import * as northwind from '../_temp-configs/northwind';
import { PageInfo } from 'src/app/shared/models';

@Injectable({
  providedIn: 'root'
})
export class AppService {

  private _myApps: AppLinkInfo[];
  private _currentApp = new BehaviorSubject(null);
  private _currentAppXHR: Observable<AppInfo>;
  private _currentAppURL: string;
  private _pages: { [key: string]: PageInfo } = {};

  currentApp$ = this._currentApp.asObservable();

  get currentApp() {
    return this._currentApp.getValue();
  }

  constructor(private httpClient: HttpClient, private authService: AuthService) {
    // this.__reCreateData(northwind);
    // this.__setTableData();
    // this.__setCountries();
  }

  getMyApps(): Observable<AppLinkInfo[]> {
    if (this._myApps) {
      return of(this._myApps);
    }
    return this.httpClient
      .get<AppLinkInfo[]>(`${environment.apiBaseUrl}/app-definitions/apps`, this.authService.getHttpHeaders()).pipe(map(apps => {
        this._myApps = apps;
        return apps;
      }));
  }

  getAppInfo(ownerName: string, appName: string): Observable<AppInfo> {
    const url = `${environment.apiBaseUrl}/app-definitions/app/${ownerName}/${appName}`;
    if (this._currentAppURL !== url) {
      this._currentAppURL = url;
      this._currentAppXHR = this.httpClient.get<AppInfo>(url, this.authService.getHttpHeaders()).pipe(
        map(appInfo => {
          this._currentApp.next(appInfo);
          return appInfo;
        }), share());
    }
    return this._currentAppXHR;
  }

  saveAppInfo(appInfo: AppInfo): Observable<number> {
    return this.httpClient
      .post<number>(`${environment.apiBaseUrl}/app-definitions/save-app`, appInfo, this.authService.getHttpHeaders());
  }

  savePageInfo(pageInfo: PageInfo, ownerName: string, appName: string): Observable<number> {
    const dto = {
      appOwnerName: ownerName,
      appName,
      pageInfo
    };
    return this.httpClient
      .post<number>(`${environment.apiBaseUrl}/app-definitions/save-page`, dto, this.authService.getHttpHeaders());
  }

  getPageInfo(ownerName: string, appName: string, pageName: string): Observable<PageInfo> {
    const url = `${environment.apiBaseUrl}/app-definitions/page/${ownerName}/${appName}/${pageName}`;
    if (this._pages[url]) {
      return of(this._pages[url]);
    }
    return this.httpClient.get<PageInfo>(url, this.authService.getHttpHeaders()).pipe(
      map(page => {
        this._pages[url] = page;
        return page;
      }));
  }

  getJsonValue(key: string): Observable<any> {
    return this.httpClient.get<any>(`${environment.apiBaseUrl}/json-data/get-value/${key}`, this.authService.getHttpHeaders());
  }

  setJsonValue(key: string, value: any): Observable<any> {
    return this.httpClient.post<any>(`${environment.apiBaseUrl}/json-data/set-value`, { key, value }, this.authService.getHttpHeaders());
  }

  /**
   * For development usage.
   */
  private async __reCreateData(appToCreate) {
    const appId = await this.saveAppInfo(appToCreate.app).toPromise();
    console.log(' appId ', appId);

    appToCreate.pages.forEach(async page => {
      const pageId = await this.savePageInfo(page, appToCreate.app.ownerName, appToCreate.app.appName).toPromise();
      console.log(' pageId ', pageId);
    });
  }

  private async __setCountries() {
    await this.setJsonValue('countries',
        {
          uk: [{ label: 'London', value: 'london' },
            { label: 'Liverpool', value: 'liverpool' },
            { label: 'Manchester', value: 'manchester' }],
          ua: [{ label: 'Lviv', value: 'lviv' }, { label: 'Kyiv', value: 'kyiv' }]
        }).toPromise();

      const countries = await this.getJsonValue('countries').toPromise();
      console.log('++>', countries);
  }

  private __setTableData() {
    this.setJsonValue('chemical-elements', wijmoApp.tableData).toPromise();
  }
}
