import { Component } from '@angular/core';
import { BaseUIComponent, propDescription, ComponentDescriptor, StyleProperties,
  ComponentExample, UIModel, DataModelProperties, Categories } from '@ngx-dynamic-components/core';
import { formatCamelStr } from 'src/app/shared';

@Component({
    selector: 'dc-detail-view', // tslint:disable-line
    template: `
    <div [ngStyle]="itemStyles">
      <div class="row" *ngFor="let prop of properties.props">
        <label class="col-lg-3">{{formatLabel(prop)}}</label>
        <div class="col-lg-9">{{getValue(prop)}}</div>
      <div>
    </div>
    `,
    styles: [`
      label {
        text-align: right;
        font-weight: bold;
      }
    `]
})
export class DetailViewComponent extends BaseUIComponent<DetailViewProperties> {
  formatLabel = formatCamelStr;

  getValue(prop: string) {
    if (Array.isArray(this.componentDataModel) && this.componentDataModel.length) {
      return this.componentDataModel[0][prop];
    }

    if (this.componentDataModel) {
      return this.componentDataModel[prop];
    }
  }
}

export class DetailViewProperties extends DataModelProperties {
  @propDescription({
    description: 'Displays properties data.',
    example: `['propA', 'propB']`,
  })
  props: string[];
}

interface DetailViewComponentConstrutor {
  new (): DetailViewComponent;
}

interface DetailViewPropertiesConstrutor {
  new (): DetailViewProperties;
}

const example: ComponentExample<UIModel<DetailViewProperties>> = {
  title: 'Text compoent example',
  uiModel: {
    type: 'worksheet:detail-view',
    containerProperties: {},
    itemProperties: {
      props: ['propA', 'propB']
    }
  },
  dataModel: {}
};

export const detailViewDescriptor: ComponentDescriptor<DetailViewComponentConstrutor, DetailViewPropertiesConstrutor>  = {
  name: 'detail-view',
  label: 'Detail veiw',
  packageName: 'worksheet',
  category: Categories.CustomControls,
  description: 'Data representation component',
  itemProperties: DetailViewProperties,
  component: DetailViewComponent,
  example
};
