import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot, Router, Resolve } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { AuthService } from 'src/app/security/services';

import { AppInfo } from '../../models';
import { AppService } from '../../services/app.service';

@Injectable({
  providedIn: 'root'
})
export class AppConfigGuard implements CanActivate {

  constructor(private authService: AuthService, private router: Router, private appService: AppService) {}

  canActivate(next: ActivatedRouteSnapshot): Observable<boolean> {
    return this.appService.getMyApps().pipe(map(apps => {
      const user = this.authService.user;
      const isValid = user && user.userName === next.params.owner && apps.map(a => a.appName).includes(next.params.appName);
      if (!isValid) {
        this.router.navigate(['/login']);
      }
      return isValid;
    }));
  }
}

@Injectable({
  providedIn: 'root'
})
export class AppConfigResolver implements Resolve<Observable<AppInfo>> {

  constructor(private appService: AppService) {}

  resolve({params}: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<AppInfo> {
    return this.appService.getAppInfo(params.owner, params.appName);
  }
}
