import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { AlertModule } from 'ngx-bootstrap/alert';

import { CoreModule, CoreService } from '@ngx-dynamic-components/core';
import { BootstrapModule } from '@ngx-dynamic-components/bootstrap';
import { MaterialModule } from '@ngx-dynamic-components/material';
import { WijmoModule } from '../wijmo/wijmo.module';

import { SharedModule } from 'src/app/shared';
import { ActionsService } from 'src/app/shared/services';
// Import components
import { CustomAppLayoutComponent } from './components/custom-app-layout/custom-app-layout.component';
import { DynamicPageComponent } from './components/dynamic-page/dynamic-page.component';
import { DefaultPageComponent } from './components/default-page/default-page.component';
import { MenuComponent } from './components/menu/menu.component';
import { AsideContentComponent } from './components/aside-content/aside-content.component';
import { AppsComponent } from './components/available-apps/apps.component';

import { CustomAppRoutingModule } from './custom-app-routing.module';
import { PageTitleResolver } from './components/dynamic-page/dynamic-page.guard';
import { DetailViewComponent, detailViewDescriptor } from './components/detail-view/detail-view.component';
import { DialogService } from '../shared/services/dialog.service';
import { AppConfigComponent } from './components/app-config/app-config.component';

@NgModule({
  declarations: [
    AppsComponent,
    AsideContentComponent,
    DefaultPageComponent,
    DynamicPageComponent,
    CustomAppLayoutComponent,
    MenuComponent,
    DetailViewComponent,
    AppConfigComponent
  ],
  imports: [
    CommonModule,
    CustomAppRoutingModule,
    SharedModule,
    FormsModule,

    PerfectScrollbarModule,
    BsDropdownModule.forRoot(),
    AlertModule.forRoot(),

    CoreModule,
    BootstrapModule,
    MaterialModule,
    WijmoModule
  ],
  providers: [PageTitleResolver, DialogService],
  entryComponents: [DetailViewComponent, AppConfigComponent]
})
export class CustomAppModule {
  constructor(private actionsService: ActionsService) {
    CoreService.registerComponent(detailViewDescriptor);
  }
}
