import { Injectable, Inject, ApplicationRef, Injector, EmbeddedViewRef, ComponentFactoryResolver } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { DynamicModalComponent } from 'src/app/shared';
import { PageInfo } from 'src/app/shared/models';

@Injectable({
  providedIn: 'root'
})
export class ModalService {

  private dialog: DynamicModalComponent;

  constructor(
    @Inject(DOCUMENT) private _document: Document,
    private componentFactoryResolver: ComponentFactoryResolver,
    private appRef: ApplicationRef,
    private injector: Injector) { }

  open(item: PageInfo, appContext) {
    this.appendModal(item, appContext);
  }

  close() {
    if (this.dialog) {
      this.dialog.modal.hide();
    }
  }

  private appendModal(item: PageInfo, appContext) {
    const componentRef = this.componentFactoryResolver.resolveComponentFactory(DynamicModalComponent).create(this.injector);
    this.dialog = componentRef.instance;
    componentRef.instance.viewConfig = {
      uiModel: item.uiModel,
      workflowConfig: {
        failOnError: true,
        include: ['@common'],
        vars: {},
        consts: {},
        workflowsMap: item.workflowsMap
      },
      dataModel: {},
      appContext
    };
    componentRef.instance.title = item.title;
    componentRef.instance.modal.onHide.subscribe(() => {
      componentRef.destroy();
    });

    this.appRef.attachView(componentRef.hostView);
    const domElem = (componentRef.hostView as EmbeddedViewRef<any>).rootNodes[0] as HTMLElement;

    this._document.body.appendChild(domElem);
    setTimeout(() => {
      componentRef.instance.openModal();
    });
  }
}
