import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { AppInfo, LinkInfo, NavItem } from '../../models';
import { ActionsService } from '../../services/actions.service';
import { AppService } from '../../services/app.service';
import { groupLinkInfos } from '../../utils';
import { LayoutConfig, MenuItem } from 'src/app/shared/models';
import { DialogService } from 'src/app/shared/services/dialog.service';
import { switchMap } from 'rxjs/operators';
import { AppConfigComponent } from '../app-config/app-config.component';


@Component({
  selector: 'app-custom-app-layout',
  templateUrl: './custom-app-layout.component.html',
  styles: [`
    :host {
      display: flex;
      flex-direction: column;
      min-height: 100vh;
      max-height: 100%;
    }

    .left-menu {
      left: auto !important;
      right: 0px !important;
    }

    .left-menu ::ng-deep .dropdown-item {
      min-width: 100px;
    }
  `]
})
export class CustomAppLayoutComponent implements OnInit {

  layoutConfig: LayoutConfig = {
    icon: 'assets/img/ca-logo-80x80.png',
    smIcon: 'assets/img/ca-logo.png',
    collapsed: true,
    appName: null
  };

  appName: string;
  appInfo$: Observable<AppInfo>;
  asideMenu = false;
  brandRouterLink: string[] = [];

  appInfo: AppInfo;

  sidebarItems: LinkInfo[];
  menuItems: NavItem[];

  get sidebar() {
    return this.sidebarItems && this.sidebarItems.length;
  }

  constructor(
    private route: ActivatedRoute,
    private dialogService: DialogService,
    private appService: AppService) {
  }

  ngOnInit() {
    this.loadAppInfo();
  }

  onEditApp() {
    const appEditDialog = this.dialogService.open(AppConfigComponent, {class: 'modal-xl full-height'});
    appEditDialog.config = this.appInfo;
    appEditDialog.init.pipe(
      switchMap(() => appEditDialog.component.save),
      switchMap((app) => this.appService.saveAppInfo(app as AppInfo))).subscribe(app => {
      this.loadAppInfo();
    });
  }

  private loadAppInfo() {
    const params = this.route.snapshot.params;
    this.appInfo$ = this.appService.getAppInfo(params.owner, params.appName);

    this.appInfo$.subscribe((appInfo => {
        this.appInfo = appInfo;
        this.appName = appInfo.appName;
        this.layoutConfig.appName = appInfo.appName;
        this.menuItems = groupLinkInfos(appInfo.menuItems);
        this.sidebarItems = appInfo.sideBarItems;
      }));
  }
}
