import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { switchMap, map } from 'rxjs/operators';
import { WorkflowConfig, UIModel } from '@ngx-dynamic-components/core';
import { DialogService } from 'src/app/shared/services/dialog.service';
import { MenuItem, PageInfo } from 'src/app/shared/models';
import { PageConfigComponent } from 'src/app/shared/components/page-config/page-config.component';
import { AppService } from '../../services/app.service';
import { ActionsService } from '../../services/actions.service';

@Component({
  selector: 'app-dynamic-page',
  template: `
    <ng-container *ngIf="page">
      <app-page-header [icon]="pageInfo.icon" [title]="pageInfo.title" [headerCommands]="headerCommands"></app-page-header>
      <ngx-dynamic-component
        [uiModel]='page.uiModel'
        [dataModel]='{}'></ngx-dynamic-component>
    </ng-container>
  `,
  styles: [`
    :host {
      height: calc(100vh - 200px);
      display: block;
    }
    ngx-dynamic-component {
      height: 100%;
      display: block;
    }
  `]
})
export class DynamicPageComponent implements OnInit {
  page$: Observable<PageInfo>;
  pageInfo: PageInfo;
  page;

  headerCommands: MenuItem[];

  constructor(
    public actionsService: ActionsService,
    private route: ActivatedRoute,
    private appService: AppService,
    private dialogService: DialogService,
    private router: Router) {}

  ngOnInit() {
    const {owner, appName} = this.route.snapshot.parent.params;
    this.page$ = this.route.params.pipe(
      map(params => params.pageId),
      switchMap(pageId =>  this.appService.getPageInfo(owner, appName, pageId)));

    this.page$.subscribe(p => {
      if (!p) {
        this.router.navigate([owner, appName]);
      } else {
        this.pageInfo = null;
        this.page = null;
        setTimeout(() => {
          this.pageInfo = p;
          this.page = this.getPage(p);
        });
      }
    });

    this.headerCommands = [{
      label: 'Edit',
      icon: 'fa-edit',
      command: this.editPage.bind(this)
    }];
  }

  editPage() {
    const pageEditDialog = this.dialogService.open(PageConfigComponent, {class: 'modal-xl full-height'});
    pageEditDialog.config = this.pageInfo;
    pageEditDialog.init.pipe(switchMap(() => pageEditDialog.component.save)).subscribe(p => {
      const page = p as PageInfo;
      const {owner, appName} = this.route.snapshot.parent.params;
      this.appService.savePageInfo(page as PageInfo, owner, appName).subscribe(() => {
        this.pageInfo = page;
        this.page = this.getPage(page);
      });
    });
  }

  private getPage({uiModel, workflowsMap}: PageInfo): {workflowConfig: WorkflowConfig, uiModel: UIModel} {
    return {
      uiModel,
      workflowConfig: {
        failOnError: true,
        include: ['@common'],
        vars: {},
        workflowsMap,
        consts: {}
      }
    };
  }
}
